@import url('https://fonts.googleapis.com/css?family=Roboto+Slab');

* { 
  box-sizing: border-box; 
}

body {
  display: block;
  font-size: calc(14px + (26 - 14) * ((100vw - 300px) / (1600 - 300)));
  overflow: hidden; 
  width: 100%;
  background-color: #ffffff;
}

section {
  display: grid;
  grid-template-columns: 50% 50%;
}

.about-image {
  position: relative;
  display: table-cell;
  width: 50%;
  height: 100vh;
  background-size: cover;
  background-position: 50% 20%;
  float: left;
}

/* On hover, add a black background color with a little bit see-through  */
.about-image:hover, .about-image:hover {
  cursor: pointer;
  box-shadow: 0 0 11px rgba(33,33,33,.2); 
}

.about-text {
  font-family: 'Roboto Slab', serif;
  vertical-align: middle;
  display: table-cell;
  width: 50%;
  align-items: center;
}

h1, h2, h3 {
  margin-top: 10px;
  margin-bottom: 10px;
  text-align: center;
  word-break: keep-all;
}

.pull-left  {
  text-align: left;
}

.pull-just {
  text-align: justify;
}

.scrolling {
  overflow: auto;
  height: 100vh;
  padding: 20px;
}

.header  {
  width: 100vw;
  display: table-cell;
  padding: 10px;
  margin: -10px;
}

span {
  display: inherit;
}

.contact {
  display: table;
  padding-top:1em;
  height: 100px;
  width: 100%;
  text-align: center;
}

.contact span {
  display: table-cell;
  vertical-align: middle;
}

.contact a { 
  text-decoration: none;
}

.contact a:link {
  color: inherit;
}

.contact a:visited {
  color: inherit;
}

.contact a:hover { 
  text-decoration: none;
  color: lightblue;
}

.contact span {
  /*  -webkit-filter: grayscale(100%);  Safari 6.0 - 9.0 
      filter: grayscale(100%);*/
  -webkit-transform: scale(1);
	transform: scale(1);
	-webkit-transition: .2s ease-in-out;
	transition: .2s ease-in-out;
}

.contact span:hover {
	/*  -webkit-filter: grayscale(0);
      filter: grayscale(0); */
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
}

#id1  {
  background-image: url(./assets/1.jpg);
}

#id2 {
  background-image: url(./assets/2.jpg);
  background-position: 80% 50%;
}

#id3 {
  background-image: url(./assets/3.mp4);
  background-position: 73% 50%;
}

#cats {
  font-size: 1.4em;
  text-align: center;
  height: 1.5em;
  border-radius: 15px;
  width: auto;
  /* don't touch the cats! */
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Caption text  */
.caption-text {
  color: #f2f2f2;
  font-size:  16px;
  padding: 8px 15px;
  position: absolute;
  bottom: 0;
  left: 0;
  text-align: center;
  width: 100%;
  text-shadow: 1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue;
}

@media screen and (max-width: 900px)  {

  body {
    overflow-x: auto;
    overflow-y: hidden;
    box-sizing: border-box;
    width: 100%;
  }
  .about-image {
    display: block;
    width: 100%;
    height: 450px;
    background-size: cover;
    background-position: 50% 40%;
    float:left;
    margin-block-end: 5%;
  }

  .about-text {
    padding: 10px;
    align-items: center;
  }
  .scrolling {
    overflow: inherit;
  }
}

.hidden
{
  display: none;
}

.tooltip {
  position: relative;
}

.tooltip .tooltiptext {
  visibility: hidden;
  /* width: 120px; */
  background-color: black;
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
  left: 50%;
  transform: translateX(-50%);
}

.tooltip:hover .tooltiptext {
  visibility: visible;
}

.tooltip .tooltiptext {
  opacity: 0;
  transition: opacity 1s;
}

.tooltip:hover .tooltiptext {
  opacity: 1;
}

.slick-slide img {
  width: 100%;
  height: 100vh;
  object-fit: cover;
  object-position: 70% 20%;
}

.slick-slide video {
  height: 100vh;
  width: 100%;
  position: relative;
  object-fit: cover;
  object-position: center;
}

.slick-slide {
  position: relative;
}